<template>
    <li @click="routeTo">
        <img
            class="recommend-item-img"
            :src="'https://statics.zhuishushenqi.com' + item.cover"
        />
        <span class="recommend-item-title">{{ item.title }}</span>
    </li>
</template>

<script>
export default {
    name: "RecommendItem",
    props: {
        item: Object,
    },
    methods: {
        routeTo() {
            this.$router.push({
                name: "BookDetail",
                params: {
                    id: this.item._id,
                },
            });
        },
    },
};
</script>

<style lang="less" scoped>
.recommend-item-img {
    width: 100%;
}
.recommend-item-title {
    display: block;
    font-size: 13px;
    text-align: center;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
</style>
