<template>
    <li>
        <div class="c-head">
            <div class="c-personal" v-if="item.author">
                <img
                    :src="
                        'https://statics.zhuishushenqi.com' + item.author.avatar
                    "
                    alt=""
                    v-if="item.author.avatar.indexOf('.png') == -1"
                />
                <img src="../assets/default.jpeg" alt="" v-else />
                <span class="c-nickname">{{ item.author.nickname }}</span>
                <span class="c-lv">Lv.{{ item.author.lv }}</span>
            </div>
            <div class="c-time">{{ time }}</div>
        </div>
        <div class="c-rating">
            <van-rate
                size="10px"
                gutter="1"
                count="5"
                color="#FFCD3E"
                void-color="#C8C7CC"
                allow-half
                readonly
                v-model="item.rating"
            />
        </div>
        <div class="c-title" v-if="item.title">{{ item.title }}</div>
        <p class="c-content" :class="{ comment: item.title ? false : true }">
            {{ item.content }}
        </p>
        <div class="c-like">
            <van-icon name="good-job" />
            <span class="count">{{ item.likeCount }}</span>
        </div>
    </li>
</template>

<script>
import { TimeDifference } from "@/api/api.js";
export default {
    name: "Comment",
    props: {
        item: Object,
    },
    data() {
        return {
            time: "",
        };
    },
    created() {
        this.time = TimeDifference(this.item.updated);
    },
};
</script>

