        flex: 8;
        display: flex;
        align-items: center;
        img {
            width: 30px;
            border-radius: 50%;
        }
        span {
            margin-left: 10px;
        }
        .c-nickname {
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
        }
        .c-lv {
            padding: 2px 4px;
            border: 1px solid #777;
            border-radius: 10px;
            font-size: 10px;
            color: #999;
        }
    }
    .c-time {
        flex: 4;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        font-size: 12px;
        color: #999;
    }
