<template>
    <div>
        <div class="bt-head">
            <div class="bt-nav">
                <van-icon name="arrow-left" @click="routeBack" />
                <van-icon name="share" />
            </div>
            <div class="bt-detail">
                <div class="d-img">
                    <img
                        :src="'https://statics.zhuishushenqi.com' + book.cover"
                        alt=""
                    />
                </div>
                <div class="d-info">
                    <div class="b-title">{{ book.title }}</div>
                    <div class="b-author">
                        <span class="author">{{ book.author }}</span>
                        <span class="line">|</span>
                        <span>{{ book.copyright }}</span>
                        <span class="line">|</span>
                        <span>{{ book.isSerial ? "连载中" : "已完结" }}</span>
                    </div>
                    <div class="b-wordCount">
                        <span class="msg">5书币/千字</span>
                        <span class="line">|</span>
                        <span>{{ conversion(book.wordCount, 0, "万字") }}</span>
                    </div>
                    <div class="b-vip">
                        <span>6元开通VIP，全书免广告!</span>
                        <van-icon name="arrow" style="bottom: -1px;" />
                    </div>
                </div>
            </div>
        </div>
        <div class="bt-contain">
            <div class="bt-card">
                <div class="bt-score" v-if="book.rating">
                    <div class="score-content">
                        <div class="score-text">
                            {{ retainOne(book.rating.score) }}
                        </div>
                        <van-rate
                            size="14px"
                            gutter="1"
                            count="5"
                            color="#FFCD3E"
                            void-color="#C8C7CC"
                            allow-half
                            readonly
                            v-model="score"
                        />
                    </div>
                    <div class="score-partake">
                        <span>{{ book.rating.tip }}</span>
                        <van-icon name="arrow" style="bottom: -1px;" />
                    </div>
                </div>
                <div class="bt-retain">
                    <span class="retain-per">{{ book.retentionRatio }}%</span>
                    <span class="retain-title">读者留存</span>
                </div>
                <div class="bt-pop">
                    <span class="pop-count">{{
                        conversion(book.latelyFollower, 1, "万")
                    }}</span>
                    <span class="pop-title">7日人气</span>
                </div>
                <div class="bt-totalpop">
                    <span class="total-count">{{
                        conversion(book.totalFollower, 1, "万")
                    }}</span>
                    <span class="total-title">累计人气</span>
                </div>
            </div>
            <div class="bt-card">
                <div class="bt-synopsis">
                    <div class="sy-head">
                        <div class="title">简介</div>
                        <ul class="sy-tags">
                            <TagItem
                                v-for="(tag, index) in book.tags"
                                :key="index"
                                :item="tag"
                                :activeIndex="index"
                            ></TagItem>
                        </ul>
                    </div>
                    <div class="sy-middle">
                        <p class="info" :class="{ show: showInfo }">
                            {{ book.longIntro }}
                        </p>
                        <van-icon
                            @click="showInfo = false"
                            v-if="showInfo"
                            class="arrow"
                            name="arrow-up"
                        />
                        <van-icon
                            @click="showInfo = true"
                            v-else
                            class="arrow"
                            name="arrow-down"
                        />
                    </div>
                    <div class="sy-foot" @click="routeTo">
                        <div class="fl">目录</div>
                        <div class="fr">
                            <span class="last-update">{{ updated }}</span>
                            <span class="book-link">{{
                                book.lastChapter
                            }}</span>
                            <van-icon name="arrow" />
                        </div>
                    </div>
                </div>
            </div>
            <div class="bt-card">
                <div class="short-comment">
                    <div class="comment-title">热门短评</div>
                    <ul class="comments">
                        <Comment
                            v-for="(comment, index) in shortComments"
                            :key="index"
                            :item="comment"
                        ></Comment>
                    </ul>
                </div>
            </div>
            <div class="bt-card">
                <div class="comment">
                    <div class="comment-title">热门书评</div>
                    <ul class="comments">
                        <Comment
                            v-for="(comment, index) in comments"
                            :key="index"
                            :item="comment"
                        ></Comment>
                    </ul>
                </div>
            </div>
            <div class="bt-card">
                <div class="recommend">
                    <div class="recommend-title">本书其他推荐</div>
                    <ul class="r-list">
                        <RecommendItem
                            v-for="(item, index) in limitRecommendList"
                            :key="index"
                            :item="item"
                        ></RecommendItem>
                    </ul>
                </div>
            </div>
        </div>
        <div class="btn">
            <div class="book-shelf">
                <div class="added" v-if="isAddShelf">
                    <van-icon class="add-icon" name="certificate" />
                    <span>已加入书架</span>
                </div>
                <div class="adding" @click="addShelf" v-else>
                    <img src="../assets/add-shelf.svg" />
                    <span>加入书架</span>
                </div>
            </div>
            <div class="book-read">
                <span @click="continueRead" v-if="isAddShelf">继续阅读</span>
                <span @click="startRead" v-else>开始阅读</span>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import types from "@/store/types.js";
import TagItem from "@/components/TagItem.vue";
import store from "@/store/index.js";
import { getData, TimeDifference } from "@/api/api.js";
import Comment from "@/components/Comment.vue";
import RecommendItem from "@/components/RecommendItem.vue";
export default {
    name: "BookDetail",
    data() {
        return {
            book: {},
            score: 0,
            showInfo: false,
            updated: "",
            isAddShelf: false,
            limitRecommendList: [],
        };
    },
    components: {
        TagItem,
        Comment,
        RecommendItem,
    },
    computed: {
        ...mapState({
            shortComments: (state) => state.bookShortComments,
            comments: (state) => state.bookComments,
            recommend: (state) => state.recommendList,
        }),
    },
    methods: {
        conversion(val, retain, str) {
            // val 要换算成...万字的数
            // retain 要保留的小数位
            // str 需补充的数字
            return (val / 10000).toFixed(retain) + str;
        },
        retainOne(val) {
            return val.toFixed(1);
        },
        ...mapActions([
            types.GET_BOOK_SHORT_COMMENT,
            types.GET_BOOK_COMMENT,
            types.GET_RECOMMEND_BOOK,
        ]),
        routeBack() {
            this.$router.push({ name: "Bookshelf" });
        },
        routeTo() {
            this.$router.push({
                name: "BookChapter",
                params: { title: this.book.title, id: this.$route.params.id },
            });
        },
        addShelf() {
            let bookshelf = JSON.parse(localStorage.getItem("bookshelf"));
            if (bookshelf) {
                bookshelf.data["book-" + this.$route.params.id] = {
                    title: this.book.title,
                    id: this.$route.params.id,
                    index: 0,
                };
            } else {
                bookshelf = {
                    data: {
                        ["book-" + this.$route.params.id]: {
                            title: this.book.title,
                            id: this.$route.params.id,
                            index: 0,
                        },
                    },
                };
            }

            localStorage.setItem("bookshelf", JSON.stringify(bookshelf));
            this.isAddShelf = true;
        },
        startRead() {
            this.$router.push({
                name: "Read",
                params: {
                    title: this.book.title,
                    id: this.$route.params.id,
                    index: 0,
                },
            });
        },
        continueRead() {
            let currentshelf = JSON.parse(localStorage.getItem("bookshelf"));
            this.$router.push({
                name: "Read",
                params: {
                    title: this.book.title,
                    id: currentshelf.data["book-" + this.$route.params.id].id,
                    index:
                        currentshelf.data["book-" + this.$route.params.id]
                            .index,
                },
            });
        },
    },
    watch: {
        recommend(val) {
            if (val.length > 0) {
                for (let index = 0; index < val.length; index++) {
                    let randomIndex = Math.floor(Math.random(val.length));
                    if (this.limitRecommendList.length < 4) {
                        this.limitRecommendList = [
                            ...this.limitRecommendList,
                            ...val.splice(randomIndex, 1),
                        ];
                    }
                }
            }
        },
        $route(to, from) {
            location.reload();
        },
    },
    beforeUpdate() {
        this[types.GET_BOOK_SHORT_COMMENT]({
            book: this.$route.params.id,
            type: "mostlike",
            start: 0,
            limit: 5,
        });
        this[types.GET_BOOK_COMMENT]({
            book: this.$route.params.id,
            type: "comment-count",
            start: 0,
            limit: 5,
        });
        this[types.GET_RECOMMEND_BOOK]({
            id: this.$route.params.id,
        });

        for (let index = 0; index < this.recommend.length; index++) {
            let randomIndex = Math.floor(Math.random(this.recommend.length));
            if (this.limitRecommendList.length < 4) {
                this.limitRecommendList = [
                    ...this.limitRecommendList,
                    ...this.recommend.splice(randomIndex, 1),
                ];
            }
        }
    },
    beforeRouteEnter(to, from, next) {
        store.state.loading = true;
        getData(
            "https://api.kele8.cn/agent/https://novel.kele8.cn/book-info/" +
                to.params.id
        )
            .then((res) => {
                store.state.loading = false;
                // 数据预处理
                let score = Number(
                    ((res.data.rating.score * 100) / 2 / 100).toFixed(1)
                );
                let update = TimeDifference(res.data.updated);

                let currentshelf = JSON.parse(
                    localStorage.getItem("bookshelf")
                );
                let isAddShelf = false;
                if (currentshelf && currentshelf.data["book-" + to.params.id]) {
                    isAddShelf = true;
                } else {
                    isAddShelf = false;
                }

                next((vm) => {
                    vm.book = res.data;
                    vm.score = score;
                    vm.updated = update;
                    vm.isAddShelf = isAddShelf;
                });
            })
            .catch((err) => {
                console.log(err);
                store.state.loading = false;
            });
    },
};
</script>

<style lang="less" scoped>
.line {
    margin: 0 5px;
}
.bt-head {
    width: 100vw;
    max-width: 100vw;
    background-image: url("../assets/bookdetail_bg.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    .bt-nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: #d9dbda;
        font-size: 20px;
        padding: 10px;
    }
    .bt-detail {
        display: flex;
        padding: 8px 20px;

        .d-img {
            flex: 4;
            img {
                width: 100%;
                vertical-align: middle;
            }
        }
        .d-info {
            flex: 8;
            margin: 5px 0 0 15px;
            text-align: left;
            color: #d9dbda;
            font-size: 14px;
            .b-title {
                color: #fff;
                font-size: 18px;
            }
            .init() {
                display: flex;
                align-items: center;
                margin-top: 10px;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
            }
            .b-author {
                .init();
                .author {
                    color: #e3bb4a;
                }
            }
            .b-wordCount {
                .init();
                .msg {
                    color: #e3bb4a;
                }
            }
            .b-vip {
                .init();
                color: #e3bb4a;
                span {
                    vertical-align: bottom;
                }
            }
        }
    }
}
.bt-contain {
    background-color: #f4f4f4;
    max-height: 62vh;
    overflow-y: scroll;
    .bt-card {
        background-color: #fff;
        display: flex;
        padding: 15px;
        margin-bottom: 10px;
        &:last-child {
            margin-bottom: 0;
        }
        .bt-score {
            text-align: left;
            .score-content {
                display: flex;
                align-items: center;
                .score-text {
                    font-size: 20px;
                    margin-right: 5px;
                }
            }
            .score-partake {
                display: flex;
                font-size: 13px;
                color: #a7acb2;
                align-items: center;
                span {
                    margin-right: 5px;
                }
            }
        }
        .btInit() {
            margin-left: 14px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            color: #666;
            text-align: left;
        }
        .bt-retain {
            .btInit();
            .retain-per {
                font-size: 16px;
            }
            .retain-title {
                font-size: 13px;
            }
        }
        .bt-pop {
            .btInit();
            .pop-count {
                font-size: 16px;
            }
            .pop-title {
                font-size: 13px;
            }
        }
        .bt-totalpop {
            .btInit();
            .total-count {
                font-size: 16px;
            }
            .total-title {
                font-size: 13px;
            }
        }

        .bt-synopsis {
            text-align: left;
            .sy-head {
                .sy-tags {
                    display: flex;
                    flex-wrap: wrap;
                    margin-top: 10px;
                    li {
                        font-size: 12px;
                        padding: 5px 7px;
                        background-color: #f0eff4;
                        color: #666;
                        margin-top: 5px;
                        margin-right: 10px;
                        border-radius: 20px;
                        &:last-child {
                            margin-right: 0;
                        }
                    }
                }
            }
            .sy-middle {
                margin: 15px 0 30px;
                font-size: 14px;
                color: #777;
                position: relative;
                .info {
                    margin: 0;
                    height: 3.6em;
                    line-height: 1.2em;
                    overflow: hidden;
                    &.show {
                        height: auto;
                    }
                }
                .arrow {
                    position: absolute;
                    right: 0;
                    bottom: -15px;
                }
            }
            .sy-foot {
                display: flex;
                align-items: center;
                .fl {
                    flex: 3;
                }
                .fr {
                    flex: 9;
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;
                    .last-update {
                        color: #e4393c;
                        font-size: 13px;
                    }
                    .book-link {
                        display: inline-block;
                        max-width: 150px;
                        font-size: 13px;
                        color: #959598;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        overflow: hidden;
                        margin-left: 5px;
                    }
                }
            }
        }

        .short-comment {
            text-align: left;
            width: 100%;
        }
        .comment {
            text-align: left;
            width: 100%;
        }
        .recommend {
            width: 100%;
            text-align: left;
            .r-list {
                max-width: 100%;
                overflow-x: hidden;
                display: flex;
                li {
                    width: 20%;
                    margin: 10px 8px;
                    img{
                        width: 100%;
                    }
                }
            }
        }
    }
}

.btn {
    width: 100vw;
    height: 8vh;
    overflow: hidden;
    position: fixed;
    left: 0;
    bottom: 0;
    display: flex;
    .book-shelf {
        flex: 5;
        padding: 1vh 0;
        border-top: 1px solid #777;
        color: #777;
        & > div {
            display: flex;
            flex-direction: column;
            .add-icon {
                flex: 6;
                display: block;
                font-size: 24px;
                height: 3vh;
                line-height: 3vh;
            }
            img {
                width: 24px;
                margin: 0 auto;
            }
            span {
                flex: 6;
                display: block;
                height: 3vh;
                line-height: 3vh;
                font-size: 12px;
            }
        }
    }
    .book-read {
        flex: 7;
        padding: 1vh 0;
        height: 8vh;
        background-color: #e4393c;
        color: #fff;
        span {
            display: inline-block;
            width: 100%;
            height: 8vh;
            line-height: 6vh;
        }
    }
}
</style>
